import React, { useEffect, useState, Component } from 'react'
import { Platform, Text, View, ActivityIndicator, Image, StyleSheet, ScrollView } from 'react-native'
import { ListItem, Input } from 'react-native-elements'
import { useDispatch, useSelector, connect } from 'react-redux'
import { bindActionCreators } from 'redux';

import NavigationService from '../../../Services/NavigationService'

//import { ConnectionState } from '../../../Stores/Device/Selectors'

import FirebaseActions from '../../../Stores/Firebase/Actions'
import { Actions, DataRefs, sortObject } from '../../../Stores/Firebase/Selectors'

import { Button, OperationsPanel, CustomListItem, ContentProp } from '../../../Components'
import Style from '../../../Containers/Cloud/CloudScreenStyle'
import { ApplicationStyles, Helpers, Metrics } from '../../../Theme'

import { Col, Row, Grid } from "react-native-easy-grid";

class Pigs extends Component {
  constructor(props){
    super(props);

    const {PigsList} = this.setData(props)
    this.state = {
      PigsList: PigsList,
      PigTag: ""
    }
  }

  getData() {
    this.props.setFirebaseError(null)

    const localDataStore = this.props.firebase.localDataStore
    this.props.getPigs(Object.keys(localDataStore.userData.farms)[0])
    this.props.getPens(Object.keys(localDataStore.userData.farms)[0])
  }

  setData(Props) {
    const localDataStore = Props.firebase.localDataStore

    var PigsList = null
    if (localDataStore && localDataStore.pigs) {
      PigsList = sortObject(localDataStore.pigs)
    }
    
    return {PigsList}
  }

  checkData(props) {
    if (!props.firebase || props.firebase.firebaseUser === null){
      NavigationService.navigate("Auth")
    }
    //Mabye call check for localdataStore get farms..
  }

  componentDidMount() {
    this.checkData(this.props);

    this.getData();
  }

  componentDidUpdate(prevProps, prevState) {
    if (this.props !== prevProps){
      this.checkData(this.props);

      if (this.props.firebase) {
        if (this.props.firebase.localDataStore) {
          if (this.props.firebase.localDataStore.pigs && JSON.stringify(this.state.PigsList)!==JSON.stringify(sortObject(this.props.firebase.localDataStore.pigs))) {
            const {PigsList} = this.setData(this.props)
            this.setState({
              PigsList: PigsList,
            })
          }
        }
      }
    }
  }

  render() {
    const IsLoading = this.props.firebase.firebaseIsLoading
    const firebaseUser = this.props.firebase.firebaseUser
    const firebaseErrorMessage = this.props.firebase.firebaseErrorMessage

    const localDataStore = this.props.firebase.localDataStore //Farmid

    const connectedDevice = null
    const connectedState = null
    const DeviceData = null

    return(
      <View
        style={[
          Helpers.mainContainer,
        ]}
      >
        <View style={[Helpers.contentContainer, Helpers.fullWidth, Helpers.fill]}>
          {firebaseErrorMessage ? (
            <Text style={ApplicationStyles.error}>{firebaseErrorMessage}</Text>
          ):(<></>)}
          {firebaseUser?(
            <View style={[Helpers.buttonContainer, Helpers.midWidth, Metrics.bottomMargin, Helpers.rowCenter] }>
              <Device_Display connectedState={connectedState}/>
              <Scanner_Display DeviceData={DeviceData}/>
              {this.state.PigsList ? (
                <View style={[Helpers.contentContainer, Helpers.fullSize, Helpers.fill]}>
                  <ScrollView style={[Helpers.fill, {paddingRight: 5}]}>
                    <View style={[Helpers.fill, Helpers.borderContainer, Metrics.smallHorizontalPadding, Metrics.smallVerticalPadding, {maxWidth:800}]}>
                      {Object.keys(this.state.PigsList).map((pig, i) => {
                        var Pen = "-"
                        var Section = "-"
                        if (this.state.PigsList[pig].pen && this.state.PigsList[pig].pen != 0) {
                          Pen = this.state.PigsList[pig].pen
                          if (localDataStore.pens && localDataStore.pens[Pen]){
                            var PenData = localDataStore.pens[Pen];
                            Section = PenData.section;
                          }
                        }
                        return(
                          <View key={i} style={[Helpers.rowCenter,]}>
                            <CustomListItem Title={pig} Subtitle={""} itemStyle={[{height:35}]} onPress={() => {
                              dispatch(FirebaseActions.setCurrentData({dataId:pig, type:DataRefs.PIGS, section:Section, dataId:Pen}))
                                NavigationService.navigate("DataDisplay")
                              }}/>

                            <CustomListItem itemStyle={[Helpers.center, {width:100, height:35}]} Title={Pen} Subtitle={""} onPress={() => {
                              this.setState({
                                PigTag: pig
                              })
                              }}/>

                            <CustomListItem itemStyle={[Helpers.center, {width:100, height:35}]} Title={Section} Subtitle={""} onPress={() => {
                              this.setState({
                                PigTag: pig
                              })
                              }}/>
                          </View>
                        )
                      })}
                    </View>
                  </ScrollView>
                  <View style={[Helpers.borderContainer, Helpers.midWidth]}>
                    <Input
                      label={"Pig-tag:"}
                      containerStyle={[{borderWidth:1}]}
                      style={[ApplicationStyles.input]}
                      onChangeText={text => {
                        this.setState({
                          PigTag: text
                        })
                      }}
                      value={
                        this.state.PigTag
                      }
                    />
                    <OperationsPanel pig={true} pigTag={this.state.PigTag} PigsList={this.state.PigsList} farm={Object.keys(localDataStore.userData.farms)[0]} localDataStore={localDataStore}/>
                  </View>
                </View>
              ):(<></>)} 
            </View>
          ):(<></>)}
        </View>
        {IsLoading &&
          <ActivityIndicator style={Helpers.loadingContainer} size='large' />
        }
      </View>
    )
  }
}

const mapStateToProps = (state) => {
  const { firebase } = state
  return { firebase }
};

const mapDispatchToProps  = (dispatch) => {
  return {
    getPens: (farmId) => dispatch(FirebaseActions.fetchStarting(Actions.FETCH, DataRefs.PENS, [farmId, null])),
    getPigs: (farmId) => dispatch(FirebaseActions.fetchStarting(Actions.FETCH, DataRefs.PIGS, farmId)),
    setFirebaseError: error => dispatch(FirebaseActions.setFirebaseError(error)),
  }
};

export default connect(mapStateToProps, mapDispatchToProps)(Pigs);
//export default Pigs;

const Device_Display = (props) => {
  if (Platform.OS === 'ios' || Platform.OS === 'android') {
    return(
      <Row size={1}>
        <Col size={1.5} style={{backgroundColor:"red"}}>
          <Row size={2.25} style={{backgroundColor:"blue"}}>
            <Text>Informations:</Text>
            <View style={{justifyContent:'center', alignItems: 'center'}}>
              <Text>test</Text>
            </View>
          </Row>
          <Row size={1.50}>
            <Col><Text>Messages</Text></Col>
            <Col><Text>Errors</Text></Col>
          </Row>
        </Col>
        <Col>
          {props.connectedState === ConnectionState.CONNECTED ? (
            <View style={{...styles.circle, backgroundColor:"green", justifyContent:'center', alignItems: 'center'}}>
              <Text>Device is</Text>
              <Text>connected</Text>
            </View>
          ):(
            <View style={{...styles.circle, backgroundColor:"red", justifyContent:'center', alignItems: 'center'}}>
              <Text>No Device</Text>
              <Text>connected</Text>
            </View>
          )}
        </Col>
      </Row>
    )
  }else {
    return(<></>)
  }
}

